@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0px;
  padding: 0px;
  min-height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: #1A1E23;
}

h1 {
  margin: 0px;
  padding: 30px;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

h2 {
  color: orange;
  font-size: 100px;
  display: block;
  font-size: 100px;
}

ul {
  display: flex;
  flex-direction: column;
  align-items: start;
  list-style-type: none;
  font-size: 30px;
}
ul li {
  padding: 10px 0;
}
ul li a {
  position: relative;
  display: block;
  padding: 4px 0;
  font-family: Lato, sans-serif;
  color: #ecf0f1;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
}
ul li a::after {
  position: absolute;
  content: "";
  top: 100%;
  left: 0;
  width: 100%;
  height: 3px;
  background: #3498db;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
}
ul li a:hover {
  color: #95a5a6;
}
ul li a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}